
.base{width:100%;height:100%;position:fixed;z-index:200;display:block
}
.load-once{background:#F87300 url(../images/mi_load.png) no-repeat center center;background-size:80px;opacity:1;transition:all .5s
}
.load-once.remove{opacity:0
}
.load{background:url(../images/mi_load.png) no-repeat center center;background-size:80px;opacity:1
}
.fade-enter-active,.fade-leave-active{transition:opacity .5s
}
.fade-enter,.fade-leave-to{opacity:0
}


