
.buy-control{width:100%;background:#fff;box-sizing:border-box;padding:10px;border-top:1px solid #ddd;font-family:'Microsoft YaHei'
}
.buy-control .title{line-height:25px;font-size:15px
}
.buy-control .price{font-size:16px;color:#FF5722;line-height:25px
}
.buy-control .desc{color:#999;font-size:12px
}
.buy-control .desc .tag{color:#FF5722
}
.buy-control .attr{width:100%;padding:10px 0 0 0
}
.buy-control .attr ul{width:100%;padding:0;margin:0
}
.buy-control .attr ul li{list-style-type:none;font-size:12px;border:1px solid #bbb;padding:6px 10px;margin:5px;display:inline-block
}
.buy-control .attr ul li.active{border:1px solid #ff5722;color:#ff5722
}


