
.tab{font-family:'Microsoft YaHei';width:100%;font-size:0;height:45px;box-sizing:border-box;background:#fff;border-top:1px solid #d5d5d5
}
.tab span{font-size:16px;color:#333;margin-top:6px;width:50%;box-sizing:border-box;text-align:center;line-height:30px;display:inline-block
}
.tab span:first-child{border-right:1px solid #d5d5d5
}
.tab span.active{color:#ff5722
}
.detail,.attr{width:100%;font-size:0
}
.detail img,.attr img{width:100%
}


