
.wripper{font-family:"Microsoft YaHei"
}
.wripper .addCart{width:100%;height:50px;background:#fff;position:fixed;bottom:0;left:0;font-size:0;border-top:1px solid #efefef
}
.wripper .addCart div{text-align:center;line-height:50px;display:inline-block;font-size:0;overflow:hidden;height:50px
}
.wripper .addCart div.home{width:15%;color:#333;background:url("../../images/svg/home.svg") no-repeat center center;background-size:25px
}
.wripper .addCart div.cart{width:15%;color:#333;background:url("../../images/svg/cart.svg") no-repeat center center;background-size:25px
}
.wripper .addCart div.add{width:70%;color:#fff;height:50px
}
.wripper .addCart div.add span{text-align:center;line-height:40px;background:#FF5722;width:94%;height:40px;display:block;margin:5px auto 0 auto;font-size:16px
}
.alert h5{margin:5px;text-align:center;font-weight:normal;color:#333;font-size:16px
}
.alert h6{margin:10px;color:#ff8a27;font-size:14px;font-weight:normal
}
.alert p{line-height:25px;font-size:14px;margin:5px;color:#666
}


